/**
 *  列表渲染中组件的预设，下列默认属性将会和传入Table组件列属性合并，减少默认设置json的内容
 *  module 组件名称
 *  prop  字段key
 *  label 中文名
 *  width 默认宽度（可选）
 *  minWidth  默认最小宽度（可选）
 *  fixed 是否固定表头（可选）
 *  sortable 是否可排序（可选）
 *  resizable 是否可以通过拖拽改变宽度（可选）
 *  headAlign  列对齐方式（'left' / 'center' / 'right' ）（可选）
 *  statusFormatter [function] 当is为status时可根据返回状态设置状态
 *  statusClick  [function] 设置点击触发事件的回调
 */

export const basicColumnsConfig = [
  {
    module: 'word',
    prop: 'name',
    label: '唯一标识',
    align: 'center',
    // width: 120,
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headAlign: 'center'
  },
  {
    module: 'icon',
    prop: 'icon',
    label: '图标',
    align: 'center',
    // width: 120,
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headAlign: 'center'
  },
  // image 图片
  {
    module: 'image',
    prop: 'city',
    label: '城市',
    align: 'center',
    width: 200,
    imgWidth: 140,
    imgHeight: 50,
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null
  },
  // input 输入框
  {
    module: 'input',
    prop: 'address',
    label: '姓名',
    align: 'center',
    width: 160,
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headAlign: 'center'
  },
  // number 数字输入框
  {
    module: 'number',
    prop: 'zip',
    label: '密码规则值',
    align: 'center',
    minWidth: 160,
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headAlign: 'center',
    max: (scope) => this.setMax(scope),
    min: (scope) => this.setMin(scope),
    handleChange: (scope) => this.handleValueChange(scope)
    // 可以使用 lodash 的 节流函数
    // import _ from 'lodash'
    // handleChange: _.throttle((scope) => {
    //   console.log('执行了组件的修改,', scope)
    //   this.handleListEdit(scope)
    // }, 1000)
  },
  // status  状态
  {
    module: 'status',
    prop: 'isEnable',
    label: '省份',
    align: 'center',
    width: 120,
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: (prop) => {
      return prop ? '展示' : '隐藏'
    },
    statusFormatter: (scope) => {
      return scope.isEnable ? 'primary' : 'success'
    },
    statusClick: (scope) => {
      console.log('column is clicked!')
    }
  },
  // switch 开关
  {
    module: 'switch',
    prop: 'checked',
    label: '权限',
    align: 'center',
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headAlign: 'center',
    statusClick: (scope) => this.handleSwitchChange(scope),
    setDisabled: (scope) => scope.param // 设置是否禁止切换按钮，返回true禁止，返回false不禁止
  },
  // link 可点击的文字链接
  {
    module: 'link',
    prop: 'link',
    label: '点击跳转',
    align: 'center',
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    headAlign: 'center',
    type: (item) => 'warning', // element-ui link type  默认为 type="primary"
    icon: 'el-icon-edit', // element-ui icon
    formatter: (scope) => {
      return scope.name === '王大虎' ? scope.name : '不是王大虎'
    },
    /*
    *  @params disabled 是否可以禁用，返回 true 不可点击， false 可以点击
    *  若为函数则使用返回值，否则直接绑定 例如 disabled: true
    */
    disabled: (scope) => {
      return scope.name ? scope.name === '王大虎' : false
    },
    /*
    *  @params underline 文字链接是否有下划线，返回 true 有， false 无
    *  若为函数则使用返回值，否则直接绑定 例如 underline: true
    */
    underline: (scope) => {
      return false
    },
    /*
    *  @params iconSlot 插入其他图标
    *  若为函数则使用返回值，返回值请返回html字符串，否则直接绑定 例如 iconSlot: '<i class="el-icon-view el-icon--right"></i>'
    */
    iconSlot: (scope) => {
      return '<i class="el-icon-view el-icon--right"></i>'
    },
    handleClick: (scope) => {
      console.log('link button clicked', scope)
    }
  },
  // 圆形颜色圆圈
  {
    module: 'circle',
    prop: 'color',
    label: '状态',
    align: 'center',
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headAlign: 'center',
    /*
    *  @params color 圆圈状态的颜色
    *  若为函数则使用返回值，否则直接绑定，直接绑定请设置css可以设置的颜色类型， 例如 color: 'rgba(144, 206, 19, 0.8)'
    */
    color: (scope) => {
      return scope.color
    }
  },
  // 下拉选框
  {
    module: 'select',
    prop: 'dict',
    label: '下拉',
    align: 'center',
    minWidth: '120',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headAlign: 'center',
    optionLabel: 'name', // 下拉选框绑定的 label 字段的key
    optionValue: 'code', // 下拉选框绑定的 value 字段的key
    /*
    *  @params setDisabled 设置下拉框是否禁用
    *  类型为函数，使用返回值
    */
    setDisabled: () => {
      return false
    },
    /*
    *  @params getOptions 设置下拉框下拉数据
    *  类型为函数，使用返回值
    */
    getOptions: (item) => {
      return this.dict['sys_user_type']
    }
  },
  {
    module: 'date',
    prop: 'date',
    label: '日期',
    align: 'center',
    width: '',
    minWidth: '150',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    statusFormatter: null,
    headerAlign: 'center',
    inputWidth: '130',
    valueFormat: 'yyyy-MM-dd',
    placeholder: '请选择日期',
    clearable: true,
    isShow: (item) => {
      return item.dict === '1'
    },
    getDisplay: (item) => {
      return item.dict !== '1' ? '暂无' : ''
    },
    setDisabled: (item) => {
      return false
    },
    handleChange: (item) => this.init(item)
  }
]
